$primaryColor: #4527a4;

// Spinner
.pebSpinnerContainer {
	font-size: 25px;
	font-weight: 600;
	color: $primaryColor;
	display: flex;
	align-items: center;
	justify-content: center;
	gap: 15px;

	.components-spinner {
		width: 30px;
		height: 30px;
		color: $primaryColor;
		margin: 0;

		circle,
		path {
			stroke-width: 5px;
		}
	}
}

// Notice
.pebApiKeyNotice {
	font-size: 16px;
	font-weight: 600;
	padding: 10px;
}

.bPlPanelBody .pebSpinnerContainer .components-spinner {
	background-color: transparent !important;
}

// Form
.bplMediaPlaceholder {

	.saveSettings:disabled,
	.saveSettings[disabled] {
		opacity: .7;
	}
}

// PDF Embed
.wp-block-peb-pdf-embed {
	.pebPDFEmbed {
		padding: 15px;
	}

	.pebFullWindow {
		position: absolute;
		width: 100%;
		height: 100vh;
		top: 0;
		left: 0;
		z-index: 999999;
	}
}